@use "layout";

$sudoku-dark: #675e73;
$sudoku-light: #8b7c99;

* {
	outline: 1px dotted #000;
}

html {
    font-family: 'Mulish', 'Muli', sans-serif;
    font-weight: 500;
}

body {
    display: flex;
    flex-direction: column;
    width: 100vw; height: 100vh;
}

header {
    height: 8em;

    background-color: #f88;


    .header-left {
        flex: 1 1 100%;
        align-self: baseline;
    }
    .header-center {
        flex: 0 1 auto;
        align-self: baseline;
        text-align: center;

        h1 {
            font-size: 3rem;
            white-space: nowrap;
            margin-bottom: 0.5rem;
        }
    }
    .header-right {
        flex: 1 1 100%;
        align-self: baseline;

        text-align: right;
    }
}

.setter {
    /* Maybe this needs a different font size. Not sure. */
}

main {
    .left-panel {
        flex: 1 1 100%;

        > ul >li::before {
            content: "â–¾ ";
            /* This can't be the right way to do this, can it? The intent is to have a drop-down tree menu. */
        }

        * {
            padding: 0;
            list-style-type: none;
        }

        form > div {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        background-color: #8f8
    }

    .sudoku {
        flex: 0 0 auto;

        /* maintain square aspect ratio */
        $size: min(50vw, 80vh);
        width: $size; height: $size;

        color: white;
        background-color: $sudoku-dark;
    }

    .right-panel {
        flex: 1 1 100%;
        background-color: #88f;
    }
}

footer {
    width: 100vw;
    height: 3em;

    font-size: 10px;
    text-align: center;
}
